import axios from "axios"; // 引用axios
import url from "./url.js"

const instance = axios.create({
  baseURL: url.baseUrl,
  timeout: 60000,
  headers:{
    "Authorization": 'Bearer ' +  window.localStorage.getItem('myToken')
  }
});

instance.interceptors.request.use(function (config) {
  if (config.headers.Authorization) {
    config.headers.Authorization = 'Bearer ' +  window.localStorage.getItem('myToken'),
    config.ContentType = "multipart/form-data"
  }
  // console.log(config)
  return config;
}, function (error) {
  // Do something with request error
  return Promise.reject(error);
});

//get请求
export function get(url, data, headers = {}) {
  return new Promise((resolve, reject) => {
    instance.get(url, data, { headers }).then((response) => {
        resolve(response);
      }).catch((err) => {
        reject(err);
      });
  });
}
//post请求
export function post(url, data = {}, headers = {}) {
  return new Promise((resolve, reject) => {
    instance.post(url, data, { headers }).then(
      (response) => {
        resolve(response);
      },
      (err) => {
        reject(err);
      }
    );
  });
}